<template>
  <div class="user">
    <!-- 头部 -->
    <div class="pt-4 flex box-border flex-wrap px-3 bg-lightBlue-500 rounded-b-lg">
      <div
        class="w-full flex-none flex justify-between items-center text-xl font-mono text-white"
        @click="goEdit"
      >
        <div class="overflow-hidden w-24 h-24 rounded-full">
          <img :src="userInfo.photo" alt="" class="w-full h-full" />
        </div>
        <div>{{ userInfo.name }}</div>
        <div class="text-xs bg-gray-400 rounded-l-2xl p-1">今日阅读5分钟</div>
      </div>
      <div
        class="flex-auto flex justify-around items-center py-4 text-xl font-mono text-gray-200"
      >
        <div class="flex flex-col items-center">
          <div class="num">{{ userInfo.art_count }}</div>
          <div class="test">动态</div>
        </div>
        <div class="flex flex-col items-center">
          <div class="num">{{ userInfo.follow_count }}</div>
          <div class="test">关注</div>
        </div>
        <div class="flex flex-col items-center">
          <div class="num">{{ userInfo.fans_count }}</div>
          <div class="test">粉丝</div>
        </div>
      </div>
    </div>
    <!-- 其他信息 -->
    <div class="px-1">
      <div class="flex justify-between px-3 items-center py-4 pt-4 border-b border-coolGray-200 border-opacity-40 font-mono">
        <div class="flex flex-col justify-center items-center text-rose-400 shadow-md px-10">
          <van-icon name="star-o" size="30" class="mb-2 " />
          <div>收藏</div>
        </div>
        <div class="flex flex-col justify-center items-center text-lightBlue-500 shadow-md px-10">
          <van-icon name="edit" size="30"  class="mb-2" />
          <div>作品</div>
        </div>
        <div class="flex flex-col justify-center items-center text-cyan-500 shadow-md px-10">
          <van-icon name="clock-o" size="30"  class="mb-2" />
          <div>历史</div>
        </div>
      </div>
      <div class="px-3 rounded-md ">
        <van-cell title="消息通知" is-link class="py-4 mb-2 rounded-2xl border-b-4 border-coolGray-200 border-opacity-40"/>
        <van-cell title="用户反馈" is-link class="py-4 my-2 rounded-2xl border-b-4 border-coolGray-200 border-opacity-40"/>
        <van-cell title="小智同学" is-link class="py-4 my-2 rounded-2xl border-b-4 border-coolGray-200 border-opacity-40" />
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex'
import { getUserInfo } from '../../../api/userInfo'
export default {
  name: 'index',
  data () {
    return {
      userImg: '',
      baseURL: 'http://ttapi.research.itcast.cn/',
      id: 0
    }
  },
  created () {
    this.getInfo()
  },
  computed: {
    ...mapState(['userInfo', 'userState'])
  },
  methods: {
    // 点击头像跳转路由，携带id
    goEdit () {
      this.$router.push('/information')
    },
    ...mapMutations(['setUserInfo']),
    async getInfo () {
      const res = await getUserInfo()
      this.setUserInfo(res.data)
    }
  }
}
</script>
